<template>
<div class="orders">
	<fixedheader :title="title"></fixedheader>
<div class="order-tabs ft-28">
	<div class="order-tab border-active">全部</div>
	<div class="order-tab">待付款</div>
	<div class="order-tab">待发货</div>
	<div class="order-tab">已发货</div>
	<div class="order-tab">已完成</div>
</div>


<div class="order">
	<div class="float bg-pad-30">
		<div class="float-left ft-28 ft-444">订单号:123456789123</div>
		<div class="float-right ft-999">待发货</div>
		<div class="clear"></div>
	</div>
	<div class="flex-box flex-center pad-20" @click="orderdetaildelivery">
		<img src="../../../static/imgs/shops/imgs/img-good4.png"/>
		<div>
			<div class="over2 ft-28">打开了房间的副驾发的发送到发送到发送到让他热腾方法驶的李开复机构开发的几个快递放是的发生的假</div>
			<div class="float ft-24 ft-999">
				<div class="float-left ">红色</div>
				<div class="float-right">X1</div>
				<div class="clear"></div>
			</div>
			<div class="price active-color">￥266.00</div>
		</div>
	</div>
	<div class="float bg-pad-30">
		<div class="float-right"><span class="ft-24">共1件商品，合计 :</span>￥1000</div>
		<div class="clear"></div>
	</div>	
</div>

</div>
</template>

<script>
import fixedheader from "../common/FixedHeader"
export default{
	data(){
		return{
			title:'全部订单',
		}
	},
	methods:{
		orderdetailpay(){
			this.$router.push('/orderdetailpay')
		},
		orderdetaildelivery(){
			this.$router.push('/orderdetaildelivery')
		},
		
		orderdetaildelivered(){
			this.$router.push('/orderdetaildelivered')
		},
		orderdetailfinish(){
			this.$router.push('/orderdetailfinish')
		}
	},
	components:{fixedheader}
}
</script>

<style scoped>
@import "../../../static/orders.css";
</style>


